.goodAttribute {
  background: #66cc33;
}

.averageAttribute {
  background: #ffcc33;
}

.lowAttribute {
  background: #ff9900;
}

.poorAttribute {
  background: #ff3333;
}

.wrestler-header {
  background-color: #7db9e8;
  background-size: 100%;
  background-image: -webkit-gradient(linear, 0% 100%, 100% 0%, color-stop(0%, #467bfb), color-stop(70%, #181818));
  background-image: -moz-linear-gradient(left bottom, #467bfb 0%, #181818 70%);
  background-image: -webkit-linear-gradient(left bottom, #467bfb 0%, #181818 70%);
  background-image: linear-gradient(to right top, #467bfb 0%, #181818 70%);
}

.push-left {
  float: left !important;
}

.wrestler-render {
  padding-top: 10px;
  width: 18%;
  margin-right: 10px;
}

.wrestler-image {
  height: 150px;
  background-size: cover;
  overflow: hidden;
  /* hide the cropped portion */
}

.wrestler-info {
  padding-top: 45px;
}

.wrestler-lastname h3 {
  color: #fff;
  font-size: 40px;
  line-height: 35px;
  font-weight: normal;
  text-transform: uppercase;
}

.wrestler-firstname h3 {
  color: #fff;
  float: none;
  display: block;
  font-size: 16px;
  text-transform: uppercase;
}

.wrestler-nickname h3 {
  color: #fff;
  float: none;
  display: block;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
}

.wrestler-header-right {
  display: inline-block;
}

.game-logo img {
  max-width: 25%;
}

td.float {
  float: right;
}

.attributeValue, .abilityValue, .skillValue {
  margin-top: -36px;
}

.panel {
  padding: 0;
}

.panel-table {
  background: #fff;
  margin-bottom: 0;
  border: none;
  table-layout: fixed;
  width: 100%;
}

#panel-comment {
  background-color: #494b5b;
  border-bottom: 2px solid #467bfb !important;
  padding: 10px;
  color: #FFF;
}

.container {
  margin: 0 auto;
  box-sizing: border-box;
}

a:link {
  cursor: pointer;
  text-decoration: none;
  transition: all ease .2s;
  color: #282936;
}

a:visited {
  cursor: pointer;
  text-decoration: none;
  transition: all ease .2s;
  color: #282936;
  text-align: center;
  padding: 0 20px 0 20px;
}

a:hover {
  cursor: pointer;
  transition: all ease .2s;
}

a:active {
  cursor: pointer;
  transition: all ease .2s;
}

p {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  letter-spacing: .01em;
  font-weight: 400;
  font-style: normal;
  font-size: 0.85em;
  line-height: 1.7;
  margin: 0 auto;
}

h1 {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-weight: 100;
  margin: 0 auto;
}

h2 {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-weight: 400;
  margin: 0 auto;
}

h3 {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-weight: 400;
  margin: 0 auto;
}
