// Bootstrap
@import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

// Imports
@import "variables";
@import "base";
@import "cake";
@import "myCake";
@import "navigation";
@import "wrestlersProfile";
@import "mobile";

// Housekeeping
@import url('https://fonts.googleapis.com/css?family=Roboto:$regular,500,700');

.container {
  margin: 0 auto;
  box-sizing: border-box
}

a:link {
  cursor: pointer;
  text-decoration: none;
  transition: all ease .2s;
  color: #282936;
}

a:visited {
  cursor: pointer;
  text-decoration: none;
  transition: all ease .2s;
  color: #282936;
  text-align: center;
  padding: 0 20px 0 20px;
}

a:hover {
  cursor: pointer;
  transition: all ease .2s;
}

a:active {
  cursor: pointer;
  transition: all ease .2s;
}

p {
  font-family: $base-font-family;
  letter-spacing: .01em;
  font-weight: $regular;
  font-style: normal;
  font-size: 0.85em;
  line-height: 1.7;
  margin: 0 auto;
}

h1 {
  font-family: $base-font-family;
  font-weight: 100;
  margin: 0 auto;
}

h2 {
  font-family: $base-font-family;
  font-weight: $regular;
  margin: 0 auto;
}

h3 {
  font-family: $base-font-family;
  font-weight: $regular;
  margin: 0 auto;
}
